.card {
    background: #fff;
    border-radius: 2px;
    margin: 1rem;
    position: relative;
    width: 250px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    margin: 10px;
  }
  
  .card > .img-container {
    /* height: 60%; */
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
    
  }
  .card > .img-container > img {
    width: 100%
  }
  
